<template>
  <div class="main">
    <h3>Qualidade vs. Velocidade</h3><a href="#" v-scroll-to="'#introduction'" style="float:right">Voltar ao topo</a>

    <p><i>Postado em 29 de janeiro de 2021</i></p>

    <img
      alt="Vue logo"
      src="@/assets/3dprintweek/camadas01.jpg"
      width="40%"
      style="
        float: right;
        margin-right: 0px;
        margin-top: 0px;
        max-width: 500px;
        min-width: 200px;
      "
    />
    <p>
      Com a orientação do objeto em relação à mesa de impressão definida, a
      altura de cada camada que será impressa é fator chave na qualidade dos
      detalhes do objeto. Quando mais finas as camadas, mais detalhes podem ser
      capturados pelos movimentos da impressora, entretanto, mais camadas serão
      necessárias para atingir a altura do objeto, consequentemente, maior o
      tempo de impressão.
    </p>
    <p>
      Se qualidade não é um fator importante, por exemplo, para peças
      funcionais, pode-se usar camadas mais altas, para reduzir o tempo de
      impressão, considerando um limite máximo baseado no diâmetro do bico de
      impressão (quando mais o diâmetro, maior altura de camada é possível
      atingir). Um cuidado que se deve tomar, é o equilibrio entre temperatura,
      altura da camada e a velocidade dos movimentos.
    </p>
    <p>
      Embora exista o risco de se perder qualidade nos detalhes, pode-se
      aumentar a velocidade de movimento dos motores que controlam as direções
      horizontais e verticais (X e Y) do hotend, mas com a necessidade de
      depositar (extrusar) plástico derretido de forma mais rápida,
      eventualmente a temperatura precisa ser calibrada. Esse equilíbrio dos
      parâmetros é o grande desafio da pessoa que quer imprimir algum objeto.
    </p>
    <p>
      As vezes, diferentes objetos, precisam de diferentes configurações, mesmo
      utilizando o mesmo material e a prática é a única forma de atingir bons
      resultados. Quanto mais uma pessoa conhecer os limites da sua impressora,
      melhor serão os objetos impressos.
    </p>
  </div>
</template>

