    <p>
      Com o material escolhido e a temperatura definida no software fatiador,
      deve-se definir a posição do objeto sobre a mesa de impressão. Geralmente,
      quando maior a superfície em contato com a mesa, menos chance do objeto
      descolar no meio da impressão (algo que acontece muito frequentemente).
    </p>
