    <p>
      Além disso, deve-se atentar se o objeto vai sofrer tensão mecânica, pois o
      objeto tem menos resistência à forças que forçam a separação das camadas.
      A configuração de preenchimento (0 significa objeto oco e 100% objeto
      totalmente maciço) pode ajudar a dar mais resistência ao objeto, assim
      como a quantidade de paredes externas, ao custo de mais material e tempo
      para impressão.
    </p>

    <p>
      Eventualmente, um objeto pode necessitar de suportes caso alguma região do
      objeto não tenha sustentação (no caso, a impressora poderia tentar
      imprimir uma camada no ar). O software fatiador pode automaticamente
      construir os suportes onde for necessário.
